import React from 'react'
import { fullPath } from '../helpers'
import { Helmet } from 'react-helmet'

const Page = ({ data, children }) => (
	<div /*data-aos="fade-left"*/>
		<Helmet
			defaultTitle="QueerIST - Núcleo queer de Estudantes do IST"
			titleTemplate="%s | QueerIST - Núcleo queer de estudantes do Instituto Superior Técnico"
			defer={false}
		>
			{/* HTML Meta Tags */}
			<title>{data.name}</title>
			<meta name="description" content={data.description} />
			{/* Google / Search Engine Tags */}
			<meta itemprop="name" content={data.name} />
			<meta itemprop="description" content={data.description} />
			<meta itemprop="image" content={fullPath(data.img_link)} />

			{/* Facebook Meta Tags */}
			<meta property="og:url" content={fullPath()} />
			<meta property="og:type" content="website" />
			<meta property="og:title" content={data.name} />
			<meta property="og:description" content={data.description} />
			<meta property="og:image" content={fullPath(data.img_link)} />

			{/* Twitter Meta Tags */}
			<meta name="twitter:card" content="summary_large_image" />
			<meta name="twitter:title" content={data.name} />
			<meta name="twitter:description" content={data.description} />
			<meta name="twitter:image" content={fullPath(data.img_link)} />
		</Helmet>
		{children}
	</div>
)

export default Page